
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mapActions} from 'vuex'
import {go} from '../../libs/router'

export default{
  data() {
    return {
      show: false,
      username: '',
      password: ''
    }
  },
  methods: {
    submit: function () {
      this.show = !this.show
      let data = {
        username: this.username,
        password: this.password
      }
      this.$store.dispatch('Login', data).then((user) => {
        if(user.error_code){
          this.$vux.toast.show({
            text: user.error_message,
            position: 'bottom',
            width: '10em',
            type: 'text'
          })
          this.show = false
        }else {
          var _this = this
          this.$vux.toast.show({
            text: '登录成功',
            type: 'success',
            position: 'default',
            onHide(){
              go({path: '/'}, _this.$router)
            }
          })
        }
      }).catch(() => {
        this.$vux.toast.show({
        text: '登录失败',
        type: 'warn'
      })
        this.show = false
      })
    }
  },
  components: {
    XInput,
    XButton
  }
}



